export default {
  data() {
    return {
      activeKey: 0,
      primaryClassification: [],
      secondaryClassification: [],
    };
  },
  methods: {
    getAllPrimaryClassification() {
      this.axios
        .get("/primaryClassification/getAll")
        .then((res) => {
          this.primaryClassification = res.data;
        })
        .catch((err) => {
          this.$notify.error({
            title: "错误",
            message: err,
            duration: 0,
          });
        });
    },
  },
  created() {
    this.getAllPrimaryClassification();
  },
};



